import React from 'react';

const Link = ({className, href, children }) => {
    const onClick = (event) => {
        if (event.metaKey || event.ctrlKey) {
            return;
          }
          
        event.preventDefault();
        window.history.pushState({}, '', href);

        const navEvent = new PopStateEvent('popstate'); //trigger Router.js to be updated
        window.dispatchEvent(navEvent); //trigger Router.js to be updated
    };

    return (
        <a onClick={onClick} className={className} href={href}>
            {children}
        </a>
    );
};

export default Link;